.startOverflow .spanStart, .endOverflow .spanEnd  {
  visibility: visible;
  background-color: rgba(255, 255, 255, 0.8);
  color: #0d47a1;
}

.startOverflow .spanStartNative, .endOverflow .spanEndNative  {
  visibility: visible;
  background-color: rgba(255, 255, 255, 0.8);
  color: #777;
}

.hidden {
  visibility: hidden;
  opacity: 0;
}

.visible {
  visibility: visible;
  opacity: 1;
}

@-webkit-keyframes loadPreviousImagesButton {
  from {opacity: 0} to {opacity: 1} 
}

@keyframes loadPreviousImagesButton {
  from {opacity: 0} to {opacity: 1} 
}

@media only screen and (-webkit-min-device-pixel-ratio: 1.3),
  only screen and (-o-min-device-pixel-ratio: 13/10),
  only screen and (-webkit-min-device-pixel-ratio: 1.25),
  only screen and (-o-min-device-pixel-ratio: 5/4),
  only screen and (min-resolution: 120dpi) {

  #root {
    font-size: 1.2em; 
  };

  .filtersBar {
    /* font-size: 1.25em; */
  }

  .filtersMenuItemText {
    text-transform: uppercase;
  }

  .SelectedMenuList {
    font-size: 1.25em;
  }
}


